<template>
  <v-container fill-height class="py-5 grey lighten-3">
    <v-layout justify-center>
      <v-card raised width="500">
        <v-toolbar flat>
          <v-toolbar-title>Input Data Cuaca Per Hari</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-text-field
            type="date"
            v-model="item.tanggal"
            label="Tanggal"
          />
                    <v-text-field
            type="number"
            v-model.number="item.kelembapanUdara"
            label="Kelembapan Udara"
          />

          <v-text-field
            type="number"
            v-model.number="item.tekananUdara"
            label="Tekanan Udara"
            step="0.01"
          />

          <v-text-field
            type="number"
            v-model.number="item.kecepatanAngin"
            label="Kecepatan Angin"/>

          <v-text-field
            type="number"
            v-model.number="item.suhu"
            step="0.1"
            label="Suhu"/>

          <v-select v-model="item.keterangan" :items="options.keterangan" label="Keterangan" />
        </v-card-text>
        <v-card-actions>
          <v-btn @click="update" flat dark color="green">Simpan</v-btn>
          <v-btn @click="$router.back()" flat dark color="pink">Batal</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import * as options from "@/models/options";
import { Cuaca__findOne, Cuaca__update } from "@/services/cuaca";
import evbus from "@/evbus";
import constraints from "@/models/constraints";

export default {
  name: 'UpdateData',
  props: ['id'],
  data: () => ({
    item: {
      tanggal: undefined,
      suhu: constraints.suhu.min,
      kecepatanAngin: constraints.kecepatanAngin.min,
      kelembapanUdara: constraints.kelembapanUdara.min,
      tekananUdara: constraints.tekananUdara.min,
      keterangan: options.keterangan[0]
    },
    options,
    constraints
  }),
  methods: {
    async loadData() {
      evbus.$emit("loading-on");
      try {
        const item = await Cuaca__findOne(this.id);
        delete item.id;
        this.item = { ...item };
        evbus.$emit("success", "Sukses memuat data");
      } catch (err) {
        evbus.$emit("error", "Gagal memuat data");
      } finally {
        evbus.$emit("loading-off");
      }
    },
    async update() {
      const payload = { ...this.item };
      evbus.$emit("loading-on");
      try {
        const item = await Cuaca__update(this.id, payload);
        evbus.$emit("success", "Sukses mengubah data");
      } catch (err) {
        evbus.$emit("error", "Gagal mengubah data");
      } finally {
        evbus.$emit("loading-off");
        this.$router.back();
      }
    }
  },
  mounted() {
    this.loadData();
  }
}
</script>

